import { formatNumber } from "../../../utils/FormattingUtils";
import { ActionEffect } from "../../actions/Action";
import { TransformationTags } from "../../calculation/TransformationTags";
import { PlayerContextState } from "../../PlayerContext";
import { grantTemporaryEffect } from "../../timetick/TemporaryEffects";
import { ItemParams } from "../Item";
import { ConsumableItem } from "./ConsumableItem";

class DefensePotion extends ConsumableItem {
  getId(): string {
    return "defensePotion";
  }

  getPicture() {
    return require("./defensePotion.png");
  }

  getBaseName(): string {
    return "Defense Potion";
  }

  getDescription(): string | undefined {
    return "A potion that hardens your skin. Temporarily raises :defense:.";
  }

  doConsumableItemEffect(
    state: PlayerContextState,
    params: ItemParams,
  ): PlayerContextState {
    const effects = this.getItemEffects(state, params);
    return grantTemporaryEffect(
      "Defense Potion",
      "Defense Potion",
      effects.duration,
      {
        params: { commonBuff: { Player: { Defense: effects.magnitude } } },
      },
    )(state);
  }

  protected getBaseItemEffects(): Record<string, ActionEffect> {
    return {
      magnitude: {
        value: 8,
        tags: [TransformationTags.TemporaryEffectMagnitude],
      },
      duration: {
        value: 180,
        tags: [TransformationTags.TemporaryEffectDuration],
      },
    };
  }

  getEffect(state: PlayerContextState, params: ItemParams): string | undefined {
    const effects = this.getItemEffects(state, params);

    return `+${formatNumber(effects.magnitude)} :defense: for ${formatNumber(
      effects.duration,
    )} sec`;
  }

  getSalePrice(): number {
    return 300;
  }

  getCraftingSchoolLevelRequirements() {
    return { Alchemy: 6 };
  }

  getCraftingMaterialsBase() {
    return {
      resources: {
        Mana: 700,
        Water: 150,
        Stone: 1200,
      },
      items: {},
    };
  }
}

export default new DefensePotion();
