import { setGameEnding } from "../../../endings/Endings";
import { getCombatVariable, setCombatVariable, setFlag } from "../../../Flags";
import { PlayerContextState } from "../../../PlayerContext";
import {
  clearTemporaryEffect,
  getTemporaryEffects,
  grantTemporaryEffect,
  hasTemporaryEffect,
} from "../../../timetick/TemporaryEffects";
import { getSecondsPlayed } from "../../../timetick/TotalTimePlayed";
import { AttackTarget } from "../../AttackTarget";
import { setDungeonFloorCompleted } from "../../dungeons/Dungeons";
import {
  aoeEnemyAttackEffect,
  fractionEnemyAttackEffect,
  getCurrentEnemyHP,
  modifyEnemyCurrentHP,
  standardEnemyAttackEffect,
} from "../../Exploration";
import { hasCurrentActiveSummon } from "../../Summoning";
import { BattlerAction, BattlerStats, Enemy } from "../Enemy";

export class Rowell extends Enemy {
  getId(): string {
    return "rowell";
  }

  getName(): string {
    return "Rowell";
  }

  getPicture() {
    return require("./rowell.png");
  }

  getBaseStats(): BattlerStats {
    return {
      maxHP: 300000,
      defense: 400,
      attack: 500,
      attackDelay: 1.3,
      dodgeChance: 0.1,
      critChance: 0.02,
    };
  }

  getNextActionPhase1(state: PlayerContextState): BattlerAction {
    if (!hasTemporaryEffect(state, "RowellBolster")) {
      return {
        name: "Bolster",
        delay: this.getAttackDelay(state),
        transform: grantTemporaryEffect("RowellBolster", "Bolster", 30, {
          isBeneficial: true,
          isEnemyTemporaryEffect: true,
          combatOnly: true,
          params: {
            commonBuff: {
              Enemy: {
                Defense: 600,
              },
            },
          },
        }),
      };
    }

    const temporaryEffects = getTemporaryEffects(state);
    if (
      temporaryEffects.filter(
        (effect) => !effect.isBeneficial && effect.isEnemyTemporaryEffect,
      ).length > 0
    ) {
      return {
        name: "Dispel Self",
        delay: this.getAttackDelay(state) * 0.1,
        transform: (state) => {
          const temporaryEffects = getTemporaryEffects(state);
          temporaryEffects.forEach((effect) => {
            if (!effect.isBeneficial && effect.isEnemyTemporaryEffect) {
              state = clearTemporaryEffect(effect.id)(state);
            }
          });
          return state;
        },
      };
    }

    const reflectChance = 0.07;
    if (
      Math.random() < reflectChance &&
      !hasTemporaryEffect(state, "EnemyReflect")
    ) {
      return {
        name: "Spell Mirroring",
        delay: this.getAttackDelay(state),
        transform: grantTemporaryEffect("EnemyReflect", "Spell Mirror", 15, {
          combatOnly: true,
          isEnemyTemporaryEffect: true,
        }),
      };
    }

    const revitalizeChance = 0.25;
    if (Math.random() < revitalizeChance) {
      return {
        name: "Divine Heal",
        delay: this.getAttackDelay(state) * 1.5,
        transform: (state: PlayerContextState) => {
          const healAmt = 5000 + Math.random() * 500;
          state = modifyEnemyCurrentHP(state, healAmt);
          const actionResult = {
            id: Math.random().toString(),
            damage: -healAmt,
            isCrit: false,
            isHit: true,
            time: getSecondsPlayed(state),
            target: AttackTarget.Enemy,
            tags: [],
          };
          state.exploration.lastEnemyActionResult = actionResult;
          state.exploration.actionResultQueue.push(actionResult);
          return state;
        },
      };
    }

    const holyJudgmentChance = 0.15;
    if (Math.random() < holyJudgmentChance) {
      return {
        name: "Holy Judgment",
        delay: this.getAttackDelay(state) * 2.5,
        transform: (state: PlayerContextState) =>
          standardEnemyAttackEffect(
            state,
            1.2,
            this.getAttack(state) * 5,
            this.getCriticalChance(state),
          ),
      };
    }

    const divineBarrageChance = 0.3;
    if (Math.random() < divineBarrageChance) {
      return {
        name: "Divine Barrage",
        delay: this.getAttackDelay(state) * 1.75,
        transform: (state: PlayerContextState) =>
          standardEnemyAttackEffect(
            state,
            1.2,
            this.getAttack(state) * 2.5,
            this.getCriticalChance(state),
          ),
      };
    }

    const smiteChance = 0.6;
    if (Math.random() < smiteChance) {
      return {
        name: "Smite",
        delay: this.getAttackDelay(state) * 1.25,
        transform: (state: PlayerContextState) =>
          standardEnemyAttackEffect(state, 1.2, this.getAttack(state) * 1.5),
      };
    }

    return {
      name: "Attack",
      delay: this.getAttackDelay(state),
      transform: (state: PlayerContextState) =>
        standardEnemyAttackEffect(state, 1.2, this.getAttack(state)),
    };
  }

  getNextActionPhase2(state: PlayerContextState): BattlerAction {
    if (!(getCombatVariable(state, "rowell_dispelled_phase_2") ?? false)) {
      state = setCombatVariable("rowell_dispelled_phase_2", true)(state);
      return {
        name: "Dispel All and Banish!",
        delay: this.getAttackDelay(state) * 0.1,
        transform: (state) => {
          const tempEffects = getTemporaryEffects(state);
          tempEffects.forEach((tempEffect) => {
            state = clearTemporaryEffect(tempEffect.id)(state);
          });
          if (hasCurrentActiveSummon(state)) {
            state = standardEnemyAttackEffect(
              state,
              100,
              9999999,
              0,
              AttackTarget.Summon,
            );
          }
          return state;
        },
      };
    }
    if (!(getCombatVariable(state, "rowell_used_big_bang_phase_2") ?? false)) {
      state = setCombatVariable("rowell_used_big_bang_phase_2", true)(state);
      return {
        name: "Big Bang",
        delay: this.getAttackDelay(state) * 4,
        transform: (state: PlayerContextState) =>
          aoeEnemyAttackEffect(
            state,
            100,
            this.getAttack(state) * 1000,
            this.getCriticalChance(state),
          ),
      };
    }

    if (!hasTemporaryEffect(state, "RowellEmpowered")) {
      return {
        name: "Blinding Speed!",
        delay: this.getAttackDelay(state),
        transform: grantTemporaryEffect(
          "RowellEmpowered",
          "Blinding Speed",
          20,
          {
            isBeneficial: true,
            isEnemyTemporaryEffect: true,
            combatOnly: true,
            params: {
              commonBuff: {
                Enemy: {
                  AttackDelayMultiplier: 0.5,
                  CritChance: 0.28,
                  Dodge: 0.3,
                },
              },
            },
          },
        ),
      };
    }

    if (Math.random() < 0.3) {
      return {
        name: "Double Attack",
        delay: this.getAttackDelay(state) * 1.5,
        transform: (state: PlayerContextState) => {
          state = standardEnemyAttackEffect(
            state,
            0.9,
            this.getAttack(state),
            this.getCriticalChance(state),
          );
          state = standardEnemyAttackEffect(
            state,
            0.9,
            this.getAttack(state),
            this.getCriticalChance(state),
          );
          return state;
        },
      };
    }

    return {
      name: "Quick Attack",
      delay: this.getAttackDelay(state),
      transform: (state: PlayerContextState) =>
        standardEnemyAttackEffect(
          state,
          1.0,
          this.getAttack(state) * 0.9,
          this.getCriticalChance(state),
        ),
    };
  }

  getNextActionPhase3(state: PlayerContextState): BattlerAction {
    if (!(getCombatVariable(state, "rowell_dispelled_phase_3") ?? false)) {
      state = setCombatVariable("rowell_dispelled_phase_3", true)(state);
      return {
        name: "Dispel All and Banish!",
        delay: this.getAttackDelay(state) * 0.1,
        transform: (state) => {
          const tempEffects = getTemporaryEffects(state);
          tempEffects.forEach((tempEffect) => {
            state = clearTemporaryEffect(tempEffect.id)(state);
          });
          if (hasCurrentActiveSummon(state)) {
            state = standardEnemyAttackEffect(
              state,
              100,
              9999999,
              0,
              AttackTarget.Summon,
            );
          }
          return state;
        },
      };
    }
    if (!(getCombatVariable(state, "rowell_used_big_bang_phase_3") ?? false)) {
      state = setCombatVariable("rowell_used_big_bang_phase_3", true)(state);
      return {
        name: "Big Bang",
        delay: this.getAttackDelay(state) * 4,
        transform: (state: PlayerContextState) =>
          aoeEnemyAttackEffect(
            state,
            100,
            this.getAttack(state) * 1000,
            this.getCriticalChance(state),
          ),
      };
    }

    if (!hasTemporaryEffect(state, "RowellTruePower")) {
      return {
        name: "Behold my True Power!",
        delay: this.getAttackDelay(state) * 2.0,
        transform: grantTemporaryEffect("RowellTruePower", "True Power", 600, {
          isBeneficial: true,
          isEnemyTemporaryEffect: true,
          combatOnly: true,
          params: {
            commonBuff: {
              Enemy: {
                Attack: 150,
                Defense: 150,
                AccuracyMultiplier: 2.0,
                AttackDelayMultiplier: 0.9,
              },
            },
          },
        }),
      };
    }
    if (!(getCombatVariable(state, "rowell_used_silence_phase_3") ?? false)) {
      return {
        name: "Silence!",
        delay: this.getAttackDelay(state) * 2.0,
        transform: (state: PlayerContextState) => {
          state = setCombatVariable("rowell_used_silence_phase_3", true)(state);
          state = grantTemporaryEffect("SilencePlayer", "Silenced", 30, {
            isBeneficial: false,
            combatOnly: true,
          })(state);
          return state;
        },
      };
    }
    if (!(getCombatVariable(state, "rowell_used_1hp_phase_3") ?? false)) {
      return {
        name: "Critical Injury",
        delay: this.getAttackDelay(state) * 1.5,
        transform: (state: PlayerContextState) => {
          state = setCombatVariable("rowell_used_1hp_phase_3", true)(state);
          return fractionEnemyAttackEffect(state, 100.0, 0.999);
        },
      };
    }

    const criticalInjuryTimer =
      getCombatVariable(state, "rowell_critical_injury_timer") ?? 0;

    const criticalInjuryChance = 0.1;
    if (Math.random() < criticalInjuryChance && criticalInjuryTimer >= 10) {
      return {
        name: "Critical Injury",
        delay: this.getAttackDelay(state) * 1.5,
        transform: (state: PlayerContextState) => {
          state = setCombatVariable("rowell_critical_injury_timer", 0)(state);
          return fractionEnemyAttackEffect(state, 100.0, 0.999);
        },
      };
    }

    const holyJudgmentChance = 0.15;
    if (Math.random() < holyJudgmentChance) {
      return {
        name: "Holy Judgment",
        delay: this.getAttackDelay(state) * 2.5,
        transform: (state: PlayerContextState) => {
          state = setCombatVariable(
            "rowell_critical_injury_timer",
            criticalInjuryTimer + 1,
          )(state);
          return standardEnemyAttackEffect(
            state,
            1.2,
            this.getAttack(state) * 5,
            this.getCriticalChance(state),
          );
        },
      };
    }

    const divineBarrageChance = 0.3;
    if (Math.random() < divineBarrageChance) {
      return {
        name: "Divine Barrage",
        delay: this.getAttackDelay(state) * 1.75,
        transform: (state: PlayerContextState) => {
          state = setCombatVariable(
            "rowell_critical_injury_timer",
            criticalInjuryTimer + 1,
          )(state);
          return standardEnemyAttackEffect(
            state,
            1.2,
            this.getAttack(state) * 2.5,
            this.getCriticalChance(state),
          );
        },
      };
    }

    const smiteChance = 0.6;
    if (Math.random() < smiteChance) {
      return {
        name: "Smite",
        delay: this.getAttackDelay(state) * 1.25,
        transform: (state: PlayerContextState) => {
          state = setCombatVariable(
            "rowell_critical_injury_timer",
            criticalInjuryTimer + 1,
          )(state);
          return standardEnemyAttackEffect(
            state,
            1.2,
            this.getAttack(state) * 1.5,
          );
        },
      };
    }

    return {
      name: "Attack",
      delay: this.getAttackDelay(state),
      transform: (state: PlayerContextState) => {
        state = setCombatVariable(
          "rowell_critical_injury_timer",
          criticalInjuryTimer + 1,
        )(state);
        return standardEnemyAttackEffect(state, 1.2, this.getAttack(state));
      },
    };
  }

  getNextAction(state: PlayerContextState): BattlerAction {
    if (getCurrentEnemyHP(state) < 100000) {
      return this.getNextActionPhase3(state);
    } else if (getCurrentEnemyHP(state) < 200000) {
      return this.getNextActionPhase2(state);
    }
    return this.getNextActionPhase1(state);
  }

  getCoinsAwarded(state: PlayerContextState): number {
    return 0;
  }

  getMonstiumAwarded(state: PlayerContextState): number {
    return 0;
  }

  getItemsAwarded(state: PlayerContextState) {
    return [];
  }

  isBoss(): boolean {
    return true;
  }

  getBackdropColor(): string | undefined {
    return "#ff8";
  }

  onDeath(
    state: PlayerContextState,
    isFromCreatures: boolean,
  ): PlayerContextState {
    state = super.onDeath(state, isFromCreatures);
    state = setFlag("wizards_arena_final_gauntlet_4_defeated")(state);
    state = setDungeonFloorCompleted(state, "wizardsArenaFloor");
    state = setGameEnding(state, "standard_ending");
    return state;
  }

  onDeathBypass(
    state: PlayerContextState,
    isFromCreatures: boolean,
  ): PlayerContextState {
    return super.onDeath(state, isFromCreatures);
  }
}

export default new Rowell();
