import { ItemBase } from "../../Item";

class NatureSpeck extends ItemBase {
  getId(): string {
    return "natureSpeck";
  }

  getPicture() {
    return require("./natureSpeck.png");
  }

  getBaseName(): string {
    return "Nature Speck";
  }

  getTags() {
    return [{ tag: "Material" }];
  }

  getDescription(): string | undefined {
    return "A small lime green gem. Full of nature elemental power.";
  }

  getEffect(): string | undefined {
    return undefined;
  }

  getSalePrice(): number {
    return 500;
  }

  getCraftingSchoolLevelRequirements() {
    return {};
  }

  getCraftingMaterialsBase() {
    return {
      resources: {},
      items: {},
    };
  }
}

export default new NatureSpeck();
