import { ItemBase } from "../../Item";

class WaterSpeck extends ItemBase {
  getId(): string {
    return "waterSpeck";
  }

  getPicture() {
    return require("./waterSpeck.png");
  }

  getBaseName(): string {
    return "Water Speck";
  }

  getTags() {
    return [{ tag: "Material" }];
  }

  getDescription(): string | undefined {
    return "A small blue gem. Full of water elemental power.";
  }

  getEffect(): string | undefined {
    return undefined;
  }

  getSalePrice(): number {
    return 500;
  }

  getCraftingSchoolLevelRequirements() {
    return {};
  }

  getCraftingMaterialsBase() {
    return {
      resources: {},
      items: {},
    };
  }
}

export default new WaterSpeck();
