import { ItemBase } from "../../Item";

class AirSpeck extends ItemBase {
  getId(): string {
    return "airSpeck";
  }

  getPicture() {
    return require("./airSpeck.png");
  }

  getBaseName(): string {
    return "Air Speck";
  }

  getTags() {
    return [{ tag: "Material" }];
  }

  getDescription(): string | undefined {
    return "A small green gem. Full of air elemental power.";
  }

  getEffect(): string | undefined {
    return undefined;
  }

  getSalePrice(): number {
    return 500;
  }

  getCraftingSchoolLevelRequirements() {
    return {};
  }

  getCraftingMaterialsBase() {
    return {
      resources: {},
      items: {},
    };
  }
}

export default new AirSpeck();
