import { ItemBase } from "../../Item";

class EarthSpeck extends ItemBase {
  getId(): string {
    return "earthSpeck";
  }

  getPicture() {
    return require("./earthSpeck.png");
  }

  getBaseName(): string {
    return "Earth Speck";
  }

  getTags() {
    return [{ tag: "Material" }];
  }

  getDescription(): string | undefined {
    return "A small brown gem. Full of earth elemental power.";
  }

  getEffect(): string | undefined {
    return undefined;
  }

  getSalePrice(): number {
    return 500;
  }

  getCraftingSchoolLevelRequirements() {
    return {};
  }

  getCraftingMaterialsBase() {
    return {
      resources: {},
      items: {},
    };
  }
}

export default new EarthSpeck();
