import { formatNumber } from "../../../../utils/FormattingUtils";
import { applyTransformations } from "../../../calculation/Calculation";
import { TransformationTags } from "../../../calculation/TransformationTags";
import { modifyEnemyCurrentHP } from "../../../exploration/Exploration";
import { PlayerContextState } from "../../../PlayerContext";
import { Resource } from "../../../Resources";
import { registerTimeTickListener } from "../../../timetick/TimeTick";
import { EquipmentSlot, getEquippedItem } from "../../Equipment";
import { ItemParams } from "../../Item";
import { EquippableItem } from "../EquippableItem";

class NatureWand extends EquippableItem {
  getId(): string {
    return "natureWand";
  }

  getPicture() {
    return require("./natureWand.png");
  }

  getBaseName(): string {
    return "Nature Wand";
  }

  getDescription(): string | undefined {
    return "A wizard's magical wand with the power of Nature. Drains enemy life.";
  }

  getAttackBonus(state: PlayerContextState, params: ItemParams): number {
    return 15;
  }

  getManaRegenBonus(state: PlayerContextState, params: ItemParams): number {
    return applyTransformations(
      [this.getId(), Resource.Mana, TransformationTags.Production],
      state,
      16.0,
    );
  }

  getMaxManaBonus(state: PlayerContextState, params: ItemParams): number {
    return 400;
  }

  getEffect(state: PlayerContextState, params: ItemParams): string | undefined {
    const amountPerSec = this.getManaRegenBonus(state, params);
    return `+15:attack:, -8 enemy :hp:/sec, +${formatNumber(amountPerSec, {
      showDecimals: true,
    })}:mana:/sec, +400 Max:mana:`;
  }

  getSlot(): EquipmentSlot {
    return EquipmentSlot.Hand;
  }

  isCraftable(state: PlayerContextState): boolean {
    return super.isCraftable(state) && state.primaryElement == "Nature";
  }

  getSalePrice(): number {
    return 1800;
  }

  getCraftingSchoolLevelRequirements() {
    return { Conjuration: 8, Enchantment: 8 };
  }

  getCraftingMaterialsBase() {
    return {
      resources: {
        Wood: 3000,
        Mana: 2500,
      },
      items: {
        natureSpeck: 1,
      },
    };
  }
}

const natureWand = new NatureWand();

registerTimeTickListener("NatureWand", (state, timeElapsedSec) => {
  if (
    getEquippedItem(state, EquipmentSlot.Hand)?.itemId === natureWand.getId()
  ) {
    state = modifyEnemyCurrentHP(state, -8 * timeElapsedSec);
  }
  return state;
});

export default natureWand;
