import { ItemBase } from "../../Item";

class LightningSpeck extends ItemBase {
  getId(): string {
    return "lightningSpeck";
  }

  getPicture() {
    return require("./lightningSpeck.png");
  }

  getBaseName(): string {
    return "Lightning Speck";
  }

  getTags() {
    return [{ tag: "Material" }];
  }

  getDescription(): string | undefined {
    return "A small purple gem. Full of lightning elemental power.";
  }

  getEffect(): string | undefined {
    return undefined;
  }

  getSalePrice(): number {
    return 500;
  }

  getCraftingSchoolLevelRequirements() {
    return {};
  }

  getCraftingMaterialsBase() {
    return {
      resources: {},
      items: {},
    };
  }
}

export default new LightningSpeck();
